import { Component, Host, h } from '@stencil/core';

@Component({
  tag: 'chat-template',
  styleUrl: './chat-template.scss',
  shadow: true,
})
export class ChatTemplate {
  render() {
    return (
      <Host>
        <div class="dav-container">
          <div class="container">
            <div class="head">
              <slot name="head">
                <chat-header></chat-header>
              </slot>
            </div>
            <div class="content">
              <slot name="content">
                <chat-component></chat-component>
              </slot>
            </div>
            <div class="footer">
              <slot name="footer">
                <chat-footer></chat-footer>
              </slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
